	import requestHttps from '../../../common/request.js'
	export default {
		data() {
			return { 				
				serverImg:getApp().globalData.serverImg,
				reigshow:false,
				sin:1,
				tels:'',
				getshow:false,
				countdown:'获取验证码',
				timestatus:false,
				code:'',
				id:''
				
			}
		},
		methods: {
			 declick:function () {
			 	
			 	 var that=this
				 that.reigshow=!that.reigshow
			},
			bindinput:function(e){
				var that=this
				console.log(e)
				that.tels=e.detail.value
			},
			countDown(){
                var that = this;
                if(!that.countdown){                    
//                     that.disabled = false;
					that.getshow=!that.getshow
                    that.timestatus = false;
                    that.countdown = '获取验证码';
                    clearInterval(that.clear);
                }else{
                    --that.countdown;
                }
            },
			gotohome:function(){
				uni.switchTab({
					url:'../../index/index'
				})
			},
			// 短信接口
			getsms:function(){
				var that=this
				// that.getshow=!that.getshow
				var tel=that.tels
				var warn=''
				var flag=false
				if(tel==''){
					warn='请输入手机号码'
				}else if(!(/^1(3|4|5|7|8)\d{9}$/.test(tel))){
					warn = "手机号格式不正确";
				}else{
					flag=true
				}
				if(flag==false){
					uni.showToast({
						title:warn,
						icon:'none'
					})
				}else{
					console.log('bbg')
					 that.countdown = 60;
                     that.timestatus = true;
					that.getshow=!that.getshow
                     that.clear = setInterval(that.countDown,1000);
					// that.code='1234'
					// that.getshow=true
					
					requestHttps({
						url:'/app/msgcode',
						method:'POST',
						data:{
							mobile:tel
						},
						success:res=>{
							console.log(res)
							that.code=res.data.data.msg_code
						}
					})
				}
				
				
			},
			
			// 注册
			formregin:function(e){
				var that=this
				var user=e.detail.value.remoddle
				var repass=e.detail.value.repass
				var repasstwo=e.detail.value.repasstwo
				var  ivit=e.detail.value.ivit
				var recod=e.detail.value.recod
				var bcod=that.code
				console.log(that.code)
				console.log('that.code')
				console.log(e)
				var flag=false
				var warn=''
				if(user==''){
					warn='请输入手机号'
				}else if(!(/^1(3|4|5|7|8)\d{9}$/.test(user))){
					warn = "手机号格式不正确";
				}else if(repass==''){
					warn ='请输入密码'
				}else if(repasstwo==''){
					warn ='请输入密码'					
				}else if(repass !==repasstwo ){
					warn='密码不一致'
				}else if(recod==''){
					warn='请输入验证码'
				}else if(recod != bcod){
					warn='验证码错误'
					console.log(bcod)
					console.log(recod)
				}else{
					flag=true
				}
				if(flag==false){
					uni.showToast({
						title:warn,
						icon:'none'
					})
				}else{
					requestHttps({
						url:'/app/register',
						method:'POST',
						data:{
								mobile:user,
								password:repass,
								re_password:repasstwo,
								reg_share_code:ivit
						},
						success:res=>{
							console.log(res)
							console.log('注册res')
							
							if(res.data.code==1){
								var arr=res.data.data
								uni.showToast({
									title:'注册成功',
									icon:'none'
								})
								uni.setStorage({
									key:"token",
									data:arr.token
								})
								uni.setStorage({
									key:"user_id",
									data:arr.user_id
								})
								uni.setStorage({
									key:'userAuthorization',
									data:true
								})
								setTimeout(function(){
									uni.switchTab({
										url:'../mycenter/mycenter'
									})
								},1000)
							}
						}
					})
				}
				
			},
			// 登录
			formlogin:function(e){
				console.log(e)
				var that=this
				var user=e.detail.value.user
				var uword=e.detail.value.uword
				var warn=''
				var flag=false
				if(user ==''){
					warn='请填写用户名'
				} else if (!(/^1(3|4|5|7|8)\d{9}$/.test(user))) {
					  warn = "手机号格式不正确";
				}else if(uword==''){
					warn='请输入密码'
				}else{
					flag=true
				}
				if(flag==false){
					uni.showToast({
						title:warn,
						icon:'none'
					})
				}else{
					requestHttps({
						url:'/app/login',
						method:'POST',
						data:{
							username:user,
							password:uword
						},
						success: res=>{
							console.log(res)
							console.log()
							var data=res.data
							var  arr=res.data.data
							if(data.code==1){
								uni.showToast({
									title:arr.message,
									icon:'none'
								})
								console.log(arr.token)
								console.log('(arr.token')
								uni.setStorage({
									key:"token",
									data:arr.users.token
								})
								uni.setStorage({
									key:'userAuthorization',
									data:true
								})
								
								getApp().globalData.userInfo=arr.users 
								getApp().globalData.userAuthorization=true
								console.log(getApp().globalData.userInfo)
								console.log('getApp().globalData.userInfo')
								if(that.sin==1){
									setTimeout(function() {
										uni.switchTab({ 
											url:'../mycenter/mycenter'
										})
									}, 1000)
								}else{
									setTimeout(function() {
										uni.navigateTo({ 
											url:'../../productdetail/productdetail?id='+that.id
										})
									}, 1000)
								}
							}else{
								uni.showToast({
									title:res.data.message,
									icon:'none'
								})
							}
							
						},
						 
						 
					})
				}
			}
			
		},
		onLoad(optis){
			var that=this
			that.sin=optis.sin
			that.id=optis.id
		}
		 
	}
